<template>
  <div class="container shifted"><center>
    <div v-show="rows.length != 0">
      <h1 class="h1">
        My planted herbs
      </h1>
      <table width="90%" BORDER=1 BORDERCOLOR="#0000FF" BORDERCOLORLIGHT="#33CCFF" BORDERCOLORDARK="#0000CC"><tbody>
      <tr>
        <td>Herb Id</td>
        <td>Estimated price</td>
        <td>Planted at</td>
      </tr>
      <tr v-for="row in rows">
        <td class="herbid">
          {{row.HerbId}}
        </td>
        <td class="price">
          {{priceOf(row.CreateAt)}}
        </td>
        <td class="time">
          {{timeAgo(row.CreateAt)}}
        </td>
        <td class="ask">
          <button v-on:click="sell(row.HerbId, priceOf(row.CreateAt))">Sell</button>
        </td>
      </tr>
      </tbody></table>
    </div>

    <h1 class="h1">
      Plant a new herb now
    </h1>
    <table>
      <tr><td>Count: <input v-model="herbCount" placeholder="Herb count"></td></tr>
      <tr><td colspan="2"><button class="action" v-on:click="add1">+1</button><button class="action" v-on:click="add2">+2</button><button class="action" v-on:click="add5">+5</button><button class="action" v-on:click="add10">+10</button><button class="action" v-on:click="add100">+100</button></td></tr>
      <tr><td colspan="2" align="center"><button class="block" v-on:click="plant">Plant now</button></td></tr>
    </table>
  </center></div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, priceOf } from '../util/funcs.js';

const uuidv4 = require('uuid/v4');

export default {
  name: 'Plant',

  data () {
    return {
      rows: [],
      herbCount: 1
    }
  },

  async created() {
    const currentUser = this.$Amplify.Auth.currentUserInfo();
    console.log('call herbListHerbs');
    API.post('rpv', '/herbMyHerbs', {'body': {'ApiVer': 0}}).then(response => {
      console.log('herbMyHerbs return : %o',  response.Herbs);
      this.rows = response.Herbs;
      console.log('rows.length: ' + this.rows.length);
    }).catch(error => {
      console.log(error);
    });
  },

  methods: {
    add1() {
      this.herbCount ++;
    },
    add2() {
      this.herbCount += 2;
    },
    add5() {
      this.herbCount += 5;
    },
    add10() {
      this.herbCount += 10;
    },
    add100() {
      this.herbCount += 100;
    },
    async plant() {
      const currentUser = await this.$Amplify.Auth.currentUserInfo();

      API.post('rpv', '/herbNewHerb', {'body': {'ApiVer': 0, 'HerbId': uuidv4(), 'AccName':currentUser.username}  }).then(response => {
        this.$router.push('/');
      }).catch(error => {
        alert('something went wrong');
        this.buildTapped = false;
        console.log(error)
      });

    },

    async sell(herbId, refPrice) {
        this.$router.push({path: '/sell', query:{'herbId': herbId, 'refPrice': refPrice }});
    },

    timeAgo(date) {
      return timeAgo(date);
    },
    priceOf(createAt) {
      return priceOf(createAt);
    }
  }
}
</script>



