<template>
  <div>
  <center>
  <div class="herbcontainer">
  <div v-for="(row, index) in rows" :key='index'>
    <div class="herbblock" :style="backColorStyleOf(index)" v-on:click="buy(row)">
      <img src="@/assets/png/herb1.png" width=50px style="vertical-align:top">
      <div class="herbid">Herb#{{row.HerbId}}</div>
      <div class="herbtitle">Ask price: {{row.Ask}}</div>
      <div class="herbprop">Planted at: {{timeAgo(row.CreateAt)}}</div>
      <div class="herbprop">Estimated price: {{priceOf(row.CreateAt)}}</div>
    </div>
   </div>
  </div>
  </center>
  </div>
</template>


<script>
import Vue from 'vue';
import Amplify, { Auth, API } from 'aws-amplify';
import { timeAgo, priceOf, backColorStyleOf } from '../util/funcs.js';

export default {
  name: 'Auction',

  data () {
    return {
      rows: [ ]
    }
  },

  async beforeMount() {
    const currentUser = await this.$Amplify.Auth.currentUserInfo();
    console.log('call herbListHerbs');
    API.post('rpv', '/herbListAsks', {'body': {'ApiVer': 0}}).then(response => {
      console.log('herbMyHerbs return : %o',  response.Asks);
      this.rows = response.Asks;
    }).catch(error => {
      console.log(error);
    });
  },


  methods: {
    async buy(ask) {
      const currentUser = await this.$Amplify.Auth.currentUserInfo();
      console.log(currentUser);
      console.log(currentUser.id);
      console.log(ask.AccId);
      if (currentUser.id == ask.AccId) {
        alert("your's herb");
      } else {
        this.$router.push({path: '/buy', query:{'herbId': ask.HerbId, 'askPrice': ask.Ask, 'askAccId': ask.AccId , 'askAccName': ask.AccName, 'createAt': ask.CreateAt}});
     }
    },

    timeAgo(date) {
      return timeAgo(date);
    },
    priceOf(createAt) {
      return priceOf(createAt);
    },
    backColorStyleOf(index) {
      return backColorStyleOf(index);
    }
  }
}
</script>



